body
{
	background-color: white;
	font-family: "Rubik","Arial",sans-serif;
	margin: 0;
	padding: 0;
	min-width: 320px;
}

.header
{
	color: white;
	width: 75%;
	margin: 0 auto;
	display: flex;
	justify-content: space-between;
	align-items: center;
}

.sectionHeader
{
	width: 100%;
	background-color: #191A1D;
}

.sectionBlackBottom
{
	width: 100%;
	background-color: #191A1D;
	padding-bottom: 150px;
	clip-path: polygon(0 0, 100% 0, 100% 100%, 0 calc(100% - 100px));
}

.sectionBlackTop
{
	width: 100%;
	background-color: #191A1D;
	padding-top: 150px;
	clip-path: polygon(0 0, 100% 100px, 100% 100%, 0 100%);
}

.sectionBlackMiddle
{
	width: 100%;
	background-color: #191A1D;
	padding: 100px 0;
	clip-path: polygon(0 0, 100% 100px, 100% 100%, 0 calc(100% - 100px));

}

.contentBlack
{
	width: 75%;
	margin: 0 auto;
	color: #ABABAB;
}

.footer
{
	width: 100%;
	background-color: #101114;
	height: 100px;
	color: #5e5e5e;
}

.toTopButton
{
	display: flex;
	align-items: center;
	justify-content: center;
	z-index: 10;
	position: fixed;
	width: 50px;
	height: 50px;
	left: 10px;
	bottom: 10px;
	background-color: #4B6CC1;
	padding: 15px 15px;
	border-radius: 20px;
	color: white;
	text-align: center;
	box-shadow: 0 0 5px 0 rgba(0,0,0,0.75);
}

.toTopButton:hover
{
	cursor: pointer;
}

.sectionWhite
{
	width: 100%;
	background-color: white;
	padding-bottom: 75px;
}

.contentWhite
{
	width: 75%;
	margin: 0 auto;
	color: black;
}

.helloContainer
{
	width: 70%;
	margin: 0 auto;
}

.hello
{
	width: 100%;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	text-align: center;
}

.li
{
	margin-bottom: 10px;
}
.li > a
{
	text-decoration: none;
	color: #DCDCDC;
}

.li > a:visited
{
	text-decoration: none;
	color: #DCDCDC;
}

.contactButton
{
	background-color: #4B6CC1;
	display: flex;
	align-items: center;
	justify-content: center;
	padding: 0 25px;
	margin: 2px;
	border-radius: 20px;
	color: white;
	height: 60px;
}

.contactButton:hover
{
	cursor: pointer;
}


.projectContainer
{
	width: 100%;
	margin: 0 auto;
	padding: 50px 0;
}

.project
{
	width: 100%;
	display: flex;
	flex-direction: row;
	align-items: center;
}

.projectImgContainer
{
	display: flex;
	align-items: center;
	justify-content: center;
	width: 60%;

}

.projectImg
{
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	width: 80%;
}

.projectImgContent
{
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	width: 100%;
	height: 100%;
	background-color: #F0F0F0;
	margin: 25px 0;
}

.projectImgContent > img
{
	width: 100%;
	height: auto;
	box-shadow: 0 0 10px 2px rgba(0,0,0,0.75);
}



.projectDescription
{
	display: flex;
	flex-direction: column;
	align-items: center;
	width: 40%;
	text-align: justify;
}

.projectDescription > p
{
	font-size: 19px;
}

.projectDescription > a
{
	font-size: 19px;
	text-decoration: none;
	color: black;
}

.projectLink
{
	background-color: #4B6CC1;
	padding: 20px 15px;
	border-radius: 20px;
	color: white;
}

@media only screen and (max-width: 900px) {
	.project
	{
		flex-direction: column;
	}

	.projectImgContainer{
		width: 100%;
	}

	.projectDescription{
		width: 100%;
	}
}

@media only screen and (max-width: 500px) {
	.header
	{
		flex-direction: column;
	}
}